import React, { Component } from 'react';
import Event from './Event';

export class EventList extends Component {
  render() {
    const { events } = this.props;
    return (
      <>
        {!events}
        <ul className="EventList">
          {Array.isArray(events) ? (
            events.map((event) => (
              <li key={event.id}>
                <Event event={event} />
              </li>
            ))
          ) : (
            <></>
          )}
        </ul>
      </>
    );
  }
}

export default EventList;
